import pagesData from '../data/pages-v2.json';
import surahNames from '../data/surah.json';
// Build surah info with start pages (computed once at module load)
export var surahs = (function () {
    var surahStartPages = {};
    pagesData.forEach(function (verses, pageIndex) {
        var page = pageIndex + 1;
        verses.forEach(function (verse) {
            var surahId = parseInt(verse.d.split('_')[0]);
            if (!surahStartPages[surahId]) {
                surahStartPages[surahId] = page;
            }
        });
    });
    return surahNames.map(function (name, index) { return ({
        number: index + 1,
        name: name,
        startPage: surahStartPages[index + 1] || 1,
    }); });
})();
// Get surah by number (1-114)
export function getSurah(number) {
    return surahs[number - 1];
}
// Get surah(s) for a given page
export function getSurahsByPage(page) {
    return surahs.filter(function (s) {
        var nextSurah = surahs[s.number];
        var endPage = nextSurah ? nextSurah.startPage - 1 : 604;
        return page >= s.startPage && page <= endPage;
    });
}
